@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*:before,*:after{box-sizing:border-box}html{overflow-y:scroll}body{background:linear-gradient(150deg, #c1bdba, #fff);font-family:"Roboto", sans-serif;min-height:100vh;min-width:98vw;color:#fff}.logo img{position:relative;text-align:center;width:150px;margin-bottom:1em}.container{background:rgba(19,35,47,0.9);padding:40px;max-width:85vw;margin:40px auto;border-radius:4px;box-shadow:0 4px 10px 4px rgba(19,35,47,0.3)}.form{list-style:none;padding:0;margin:0 0 40px 0}.form:after{display:table;clear:both}button{border:0;border-radius:6px;padding:15px;background:#1ab188;color:#ffffff}button:hover,button:focus{background:#0d5844}h1{text-align:center;font-weight:600;margin:0 0 40px}label{color:#a0b3b0;transition:all 0.25s ease}input{font-size:22px;display:block;width:100%;height:100%;padding:5px 10px;background:none;background-image:none;border:1px solid #a0b3b0;color:#fff;border-radius:0;transition:border-color 0.25s ease, box-shadow 0.25s ease}input:focus{outline:0;border-color:#1ab188}input[type="date"]::-webkit-calendar-picker-indicator{color:rgba(0,0,0,0);opacity:1;display:block;background:url(https://mywildalberta.ca/images/GFX-MWA-Parks-Reservations.png) no-repeat;width:20px;height:20px;border-width:thin}.fechas:after{content:"";display:table;clear:both}.fechas>div{float:left;width:48%;margin-right:4%}.fechas>div:last-child{margin:0}.medidas{display:flex;text-align:center}.medidas>.medida{width:33.333%;margin:10px 0;border-right:1px solid #a0b3b0}.medidas>.medida:last-child{border:none}.medidas>.medida>.numero{font-size:2em;color:#1ab188}.hidden{display:none}.loading{text-align:center}.loading:after{content:" .";-webkit-animation:dots 1s steps(5, end) infinite;animation:dots 1s steps(5, end) infinite}@-webkit-keyframes dots{0%,20%{color:rgba(0,0,0,0);text-shadow:0.25em 0 0 rgba(0,0,0,0),0.5em 0 0 rgba(0,0,0,0)}40%{color:white;text-shadow:0.25em 0 0 rgba(0,0,0,0),0.5em 0 0 rgba(0,0,0,0)}60%{text-shadow:0.25em 0 0 white,0.5em 0 0 rgba(0,0,0,0)}80%,100%{text-shadow:0.25em 0 0 white, 0.5em 0 0 white}}@keyframes dots{0%,20%{color:rgba(0,0,0,0);text-shadow:0.25em 0 0 rgba(0,0,0,0),0.5em 0 0 rgba(0,0,0,0)}40%{color:white;text-shadow:0.25em 0 0 rgba(0,0,0,0),0.5em 0 0 rgba(0,0,0,0)}60%{text-shadow:0.25em 0 0 white,0.5em 0 0 rgba(0,0,0,0)}80%,100%{text-shadow:0.25em 0 0 white, 0.5em 0 0 white}}

